___CSS_LOADER_EXPORT___.push([module.id, `html, body {
  margin: 0;
  padding: 0;
  background-color: #000;
  overflow: hidden; }

.game-container {
  max-width: 512px;
  margin: 50px auto 0; }

.game-controls {
  max-width: 512px;
  margin: auto;
  padding: 10px;
  color: #fff; }
  .game-controls label {
    float: left;
    margin-right: 10px; }
  .game-controls .form-row {
    clear: left; }
    .game-controls .form-row > div {
      float: left;
      margin-right: 20px; }
    .game-controls .form-row.link-inventory label {
      width: 70px; }
  .game-controls input {
    outline: 0; }
    .game-controls input[type=number] {
      width: 50px;
      margin-right: 10px; }

canvas {
  display: block;
  margin: auto;
  border: 1px solid #ccc; }

@media only screen and (max-width: 512px) {
  .game-container {
    margin: 5px auto; } }
